.drag-el {
    position: relative;
}

.drag-el:before {
    content: '.';
    position: absolute;
    right: 50%;
    top: 50%;
    transform: translate(-50%, -100%);
    font-size: 20px;
    line-height: 20px;
    color: var(--gray);
    cursor: grabbing;
    text-shadow: 0 5px var(--gray), 0 10px var(--gray), 5px 0 var(--gray), 5px 5px var(--gray), 5px 10px var(--gray);
}